body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #b4c0c16e;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container .header {
  position: absolute;
  left: 40%;
}
.container .button {
  position: absolute;

  width: 200px;
  height: 50px;
  background: #ef7917;
  border-radius: 2px;
  color: #fff;
  font-size: 20px;
  padding: 9px;
  border: none;
  outline: none;
  border-radius: 50px;
  box-shadow: 10px 10px 10px black;
}
.button:hover {
  cursor: pointer;
}
.container .first {
  top: 20%;
  left: 45%;
}
.container .second {
  top: 40%;
  left: 45%;
}

.container .third {
  top: 60%;
  left: 45%;
}

.container .forth {
  top: 80%;
  left: 45%;
}

.container .tooltip {
  height: 30px;
  font-size: large;
  color: #ef7917;
}
